@font-face {
    font-family: 'lalezar';
    src: url(Lalezar-Regular.otf);
}

body{
    margin: 0;
    font-family: 'lalezar';
    text-align: right;
}

.primary{
    border-radius: 7px;
    background-color: #3646D3;
    padding: 12px 50px;
    border: none;
    color: white;
}

.secondary{
    color: white;
    border: none;
    border-radius: 7px;
    background-color: #474747;
    padding: 12px 50px;
}

.header{
    display: flex;
    justify-content: space-between;
    background-color: #D9D9D9;
    width: 100%;
    position: fixed;
}

.button-container{
    display: flex;
    margin-right: 7rem;
    align-items: center;
    gap: 2rem;
}

.button-container Button{
    height: 2.5rem;
}

.header h2{
    margin-left: 7rem;
}

.footer{
    background-color: #18191B;
    width: 100%;
    height: 20rem;
    color: #DADADA;
}

.row1{
    display: flex;
    justify-content: space-between;
    padding: 1rem 7rem;
}
.row1 p{font-size: 1.3rem;}

.row2{
    display: flex;
    padding: 1rem 7rem;
    margin-left: 54.8rem;
    text-align: right;
    gap: 10rem;
}

.column1{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-top: 1rem;
}

.intro{
    margin: 0rem 7rem;
    display: flex;
    justify-content: space-between;
    padding: 8rem 0;
    align-items: center;
}
.intro p{
    color: #525252;
    font-size: 1.1rem;
}
.width{width: 35%;}

.index img{
    order: 1;
}

.cards{
    display: flex;
    justify-content: space-between;
    margin: 2rem 7rem;
}

.card{
    background-color: #D9D9D9;
    width: 8rem;
    height: 10rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 1rem;
}

.icon{
    font-size: 2rem;
}